.nav{
    position: fixed;
    top: 0;
    padding: 13px;
    width: 100%;
    height: 30px;
    z-index: 1;

    /* Animation */
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav_contents{
    display: flex;
    justify-content: space-between;
}
.nav_logo{
    position: fixed;
    left: 0;
    top: 3px;
    width: 90px;
    padding-left: 20px;
    object-fit: contain;
    cursor: pointer;
}
.nav_avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    height: 30px;
    cursor: pointer;
}
.nav_black{
    background-color: black;
}